<template>
<transition name="search">
    <div class="search">
       <!--头部-->
        <head-menu class="sort-header">
        <i class="iconfont icon-back" slot="left" @click="searchbackHome"></i>
        <search-box slot="center" @query="getQuery"/>
        </head-menu>
         <!--滚动插件-->
         <my-scroll ref="scroll">
                <!--存历史内容区-->
                <search-history v-show="!query" @remove-item="updateScroll" @show-confirm="showConfirm" ref="history"/>
                <!--历史搜索出来的内容区-->
                <search-result :query="query" v-show="query"></search-result>
         </my-scroll>
    </div>
</transition>
</template>

<script>
//内部引用
import SearchHistory from "views/search/modular/SearchHistory";
import SearchResult from "views/search/modular/SearchResult";
//外部组件
import HeadMenu from "components/common/headmenu/HeadMenu";
import SearchBox from "components/content/search/SearchBox";
import MyScroll from "components/common/scroll/MyScroll";
export default {
  name: "FindPage",
  components:{
      HeadMenu,
      SearchBox,
      SearchHistory,
      SearchResult,
      MyScroll
  },
  data() {
      return {
        query: ''
      };
  },
  methods:{
      //返回上一页
      searchbackHome(){
          this.$router.back();
      },
      //获取input输入的值保存到query里面
      getQuery(query) {
        this.query = query;
      },
      //更新滚动条，好像没啥作用
      updateScroll() {
        this.$refs.scroll.update();
      },
      //点击清理所有历史数据
      showConfirm() {
        this.$refs.history.clear();
        this.$refs.history.update();
      },
  }
};
</script>
<style lang="scss" scoped>
 .search {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index:1200;
    background-color: #f5f5f5;

   .sort-header{
        background: linear-gradient(90deg,#27a6fa,#8e71f5);
        background: -webkit-linear-gradient(left,#27a6fa,#8e71f5);
        color: #fff;
    }

  }
  .search-enter-active,
  .search-leave-active {
      transition: all 0.3s;
  }

  .search-enter,
  .search-leave-to {
     transform: translate3d(100%, 0, 0);
  }
</style>