<template>
<div class="history" v-if="historys.length">
    <!--判断是否有值-->
    <h4 class="history-title">历史搜索</h4>
    <!--搜索出来的数据-->
    <transition-group class="g-list" name="list" tag="ul">
        <li class="g-list-item" v-for="item in historys" :key="item">
            <span class="g-list-text">{{item}}</span>
            <i class="iconfont icon-delete"  @click.stop="removeItem(item)"> </i>
        </li>
    </transition-group>
    <!--清理所有历史记录-->
    <a href="javascript:;" class="history-btn"  @click="showConfirm"><i class="iconfont icon-clear"></i>清空历史搜索</a>
</div>
</template>

<script>
  import storage from '@/network/storage';
  import {SEARCH_HISTORY_KEYWORD_KEY} from '@/views/search/config';
  import {searchMixin} from '@/commonjs/mixin';
  export default {
    name: 'SearchHistory',
    mixin: [searchMixin],
     data() {
      return {
        historys: []
      };
    },
    created() {  
      this.getKeyword();
    },
    methods:{
      //update和clear给父组件清理并从新获取值
      update() {
        console.log('获取');
        this.getKeyword();
      },
      clear() {
        console.log('清理');
        storage.remove(SEARCH_HISTORY_KEYWORD_KEY);
      },  
      //从localStorage获取值,然后赋值给列表
      getKeyword() {
        this.historys = storage.get(SEARCH_HISTORY_KEYWORD_KEY, []);
         console.log(this.historys,"wwww");
      },
      //清除单个历史记录
      removeItem(item) {
        //清理完，重新设置值
        this.historys = this.historys.filter(val => val !== item);
        storage.set(SEARCH_HISTORY_KEYWORD_KEY, this.historys);
        //然后更新一下滚动条
        setTimeout(() => {
          this.$emit('remove-item', item);
        }, 100);
      },

      //点击清除全部历史记录
      showConfirm() {
        this.$emit('show-confirm');
      },


    }
  };
</script>

<style lang="scss" scoped>
.history {
    padding-bottom: 30px;
    background-color: #fff;

    &-title {
        height: 34px;
        line-height: 34px;
        padding: 0 10px;
        font-size:14px;
        font-weight: bold;
    }

    &-btn {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction:row;
        width: 80%;
        height: 40px;
        background: none;
        border: 1px solid #ccc;
        border-radius: 4px;
        margin: 0 auto;
        color: #686868;

        .iconfont {
        margin-right: 5px;
        }
    }
}

  .g-list {
    border-top: 1px solid #e5e5e5;
    border-bottom: 1px solid #e5e5e5;
    margin-bottom: 20px;
  }

  .list {
        &-enter-active,
        &-leave-active {
        transition: height 0.1s;
        }

        &-enter,
        &-leave-to {
        height: 0;
        }
  }

.g-list {
  padding-left: 10px;
}
.g-list-item {
  overflow: hidden;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 44px;
  padding-right: 10px;
  border-bottom: 1px solid #e5e5e5;
  color: #686868;

  &:last-child {
    border-bottom: none;
  }
}
.g-list-text {
  flex: 1;
  line-height: 1.5;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

</style>
