<template>
  <div class="result">
     <!--数据还没完成先加载中-->
    <div class="loading-container" v-show="loading">
      <loading :text="logings"></loading>
    </div>
    <!--$_search_selectItem用来点击进入内容页和保存历史记录-->
    <ul class="g-list" v-show="!loading && results.length">
      <li class="g-list-item" v-for="(item, index) in results" :key="index"
        @click="$_search_selectItem(item[0])"
      >
        <span class="g-list-text">{{item[0]}}</span>
      </li>
    </ul>
    <!--判断没有数据情况下-->
    <div class="no-result" v-show="!loading && !results.length">搜索没有该内容</div>
  </div>
</template>

<script>
  //外部组件
  import Loading from "components/common/loading/Loading";
  //请求搜索出来的数据
  import {getSearchResult} from '@/network/search';
  //进入内容页和保存历史记录
  import {searchMixin} from '@/commonjs/mixin';
  export default {
    name: 'SearchResult',
    mixins: [searchMixin],  
    components: {
       Loading
    },
    data() {
      return {
        results: [],
        loading: true,
        logings:'搜索中'
      };
    },
    props: {
      query: {
        type: String,
        default: ''
      }
    },
    watch: {
      query(query) {
        this.getResults(query);
      }
    },
    methods: {
        //用于实时监听输入的内容是否有数据
        getResults(keyword) {
            //如果没有数据的话就return，否则就下一步
            if (!keyword) {
                return 
            }
            this.loading = true;
            //当（keyword）过来后,就可以请求数据
            getSearchResult(keyword).then(data => {
                if (data) {
                    this.results = data;
                    this.loading = false;
                }
            });
        }
    }
  };
</script>

<style lang="scss" scoped>
// list
.g-list {
  padding-left: 10px;
}
.g-list-item {
  overflow: hidden;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 44px;
  padding-right: 10px;
  border-bottom: 1px solid #e5e5e5;
  color: #686868;

  &:last-child {
    border-bottom: none;
  }
}
.g-list-text {
  flex: 1;
  line-height: 1.5;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.no-result{
    text-align: center;
    line-height: 30px;
    padding-top: 97px;
}
</style>
